const canvas = {}
let id = ''

let ctx = null
let canvasId = ''
let _this = undefined

/**
 *  canvas
 * @param {String} id - <canvas />  canvas-id  id
 */
canvas.init = (id, that) => {
  ctx = uni.createCanvasContext(id, that)
  canvasId = id
  _this = that
  return ctx
}

/**
 * 
 * @param {Object} option
 * @param {Number} option.x  x  px
 * @param {Number} option.y  y  px
 * @param {Number} option.width  px
 * @param {Number} option.height  px
 * @param {Number} option.borderRadius  px
 * @param {Number} option.backgroundColor 
 */
canvas.drawFillRect = option => {
  return new Promise((resolve, reject) => {
    // console.log('', option)
    const x = parseFloat(option.x)
    const y = parseFloat(option.y)
    const width = parseFloat(option.width)
    const height = parseFloat(option.height)
    const borderRadius = parseFloat(option.borderRadius) || 0
    const backgroundColor = option.backgroundColor || '#000'

    let setShadow = undefined
    if (option.setShadow) {
      setShadow = option.setShadow
    }

    //  borderRadius 
    if (width < borderRadius * 2 || height < borderRadius * 2) {
      borderRadius = Math.min(width, height) / 2
    }

    ctx.save() // 
    ctx.beginPath() // fillstroke

    // 
    /**
     * 
     * @param {Number} x  x  
     * @param {Number} y  y  
     * @param {Number} r 
     * @param {Number} sAngle 3 
     * @param {Number} eAngle 
     * @param {Boolean} counterclockwise false
     */
    ctx.arc(x + borderRadius, y + borderRadius, borderRadius, Math.PI, 1.5 * Math.PI)

    // 
    /**
     * x
     * y
     */
    ctx.lineTo(x + Math.abs(width - borderRadius), y) // 

    // 
    ctx.arc(x + Math.abs(width - borderRadius), y + borderRadius, borderRadius, 1.5 * Math.PI, 2 * Math.PI)

    // 
    ctx.lineTo(x + width, y + Math.abs(height - borderRadius))

    // 
    ctx.arc(x + Math.abs(width - borderRadius), y + Math.abs(height - borderRadius), borderRadius, 0, 0.5 * Math.PI)

    // 
    ctx.lineTo(x + borderRadius, y + height)

    // 
    ctx.arc(x + borderRadius, y + Math.abs(height - borderRadius), borderRadius, 0.5 * Math.PI, Math.PI)

    // 
    ctx.lineTo(x, y + borderRadius)

    // 
    // offsetX Number 
    // offsetY Number 
    // blur Number 0~100 
    // color Color 
    if (setShadow) {
      ctx.setShadow(setShadow)
    } else {
      ctx.setShadow(0, 0, 0, '#fff')
    }

    ctx.setFillStyle(backgroundColor) // 
    ctx.fill() // 

    // ctx.setStrokeStyle(backgroundColor) // 
    // ctx.stroke() // 
    ctx.closePath() // 

    resolve()
  })
}

/**
 * 
 * @param {Object} option
 * @param {String} image
 * @param {Number} x  x  px
 * @param {Number} y  y  px
 * @param {Number} width  px
 * @param {Number} height  px
 * @param {Number} borderRadius  px
 */
canvas.drawImage = option => {
  // console.log('', option)
  return new Promise(async (resolve, reject) => {
    const image = option.image
    const x = parseFloat(option.x)
    const y = parseFloat(option.y)
    const width = parseFloat(option.width)
    const height = parseFloat(option.height)
    const borderRadius = parseFloat(option.borderRadius) || 0

    let setShadow = undefined
    if (option.setShadow) {
      setShadow = option.setShadow
    }

    //  borderRadius 
    if (width < borderRadius * 2 || height < borderRadius * 2) {
      borderRadius = Math.min(width, height) / 2
    }

    ctx.save() // 
    ctx.beginPath() // fillstroke

    // 
    ctx.arc(x + borderRadius, y + borderRadius, borderRadius, Math.PI, 1.5 * Math.PI)

    // 
    ctx.arc(x + Math.abs(width - borderRadius), y + borderRadius, borderRadius, 1.5 * Math.PI, 2 * Math.PI)

    // 
    ctx.arc(x + Math.abs(width - borderRadius), y + Math.abs(height - borderRadius), borderRadius, 0, 0.5 * Math.PI)

    // 
    ctx.arc(x + borderRadius, y + Math.abs(height - borderRadius), borderRadius, 0.5 * Math.PI, Math.PI)

    ctx.setStrokeStyle('rgba(0,0,0,0)')
    ctx.stroke() // 

    ctx.clip() //  clip()  save()  restore() 

    ctx.closePath() // 

    // 
    /**
     * @param {String} imageResource 
     * @param {Number} dx canvas X 
     * @param {Number} dy canvas Y 
     * @param {Number} dWidth 
     * @param {Number} dHeight 
     */
    await ctx.drawImage(image, x, y, width, height)

    if (setShadow) {
      ctx.setShadow(setShadow)
    } else {
      ctx.setShadow(0, 0, 0, '#fff')
    }

    ctx.restore() // 

    ctx.closePath() // 

    resolve()
  })
}

/**
 * 
 * @param {Object} option
 * @param {Number} option.x  x  px
 * @param {Number} option.y  y  px
 * @param {String} option.color 
 * @param {String} option.fontWeight normalbold
 * @param {Number} option.fontSize  px
 * @param {Number} option.lineHeight  px
 * @param {Number} option.maxLine 
 * @param {Number} option.maxWidth 
 * @param {String} option.fontFamily
 * @param {String} option.fontStyle
 */
canvas.drawText = async option => {
  return new Promise((resolve, reject) => {
    const text = option.text
    const x = option.x
    const y = option.y
    const color = option.color || '#000'
    const fontStyle = option.fontStyle || 'normal'
    const fontVariant = option.fontVariant || 'normal'
    const fontWeight = option.fontWeight || 'normal'
    const fontSize = parseFloat(option.fontSize) || 16
    const lineHeight = parseFloat(option.lineHeight) || 21
    const fontFamily = option.fontFamily || 'sans-serif'
    let maxWidth = undefined
    if (option.maxWidth) {
      maxWidth = parseFloat(option.maxWidth)
    }
    let maxLine = undefined
    if (option.maxLine) {
      maxLine = parseInt(option.maxLine)
    }
    let setShadow = undefined
    if (option.setShadow) {
      setShadow = option.setShadow
    }

    ctx.font = `${fontStyle} ${fontVariant} ${fontWeight} ${fontSize}px ${fontFamily}`

    ctx.setFillStyle(color)
    if (!maxLine) {
      ctx.fillText(text, x, y)
    } else {
      let currentText = ''
      let currentLine = 1
      let currentLineHeight = 0

      for (let i = 0, length = text.length; i < length; i++) {
        currentText += text[i]
        if (ctx.measureText(currentText + text[i + 1]).width > maxWidth && currentLine < maxLine) {
          ctx.fillText(currentText, x, y + currentLineHeight)
          currentText = ''
          currentLine++
          currentLineHeight += lineHeight
        } else if (text[i + 1] !== undefined && ctx.measureText(currentText + text[i + 1]).width > maxWidth && currentLine >= maxLine) {
          ctx.fillText(currentText.substr(0, currentText.length - 1) + '...', x, y + currentLineHeight)
          currentText = ''
          break
        }
      }

      if (currentText) {
        ctx.fillText(currentText, x, y + currentLineHeight)
      }

      if (setShadow) {
        ctx.setShadow(setShadow)
      } else {
        ctx.setShadow(0, 0, 0, '#fff')
      }
    }

    resolve()
  })
}

canvas.draw = callback => {
  return new Promise((resolve, reject) => {
    ctx.draw(false, () => {
      uni.canvasToTempFilePath(
        {
          canvasId,
          success: res => {
            console.log('canvasToTempFilePath success', res)
            // res.tempFilePath
            resolve(res.tempFilePath)
            if (callback) {
              callback(res.tempFilePath)
            }
          },
          fail: err => {
            console.log('canvasToTempFilePath error', err)
            reject(err)
          },
        },
        _this
      )
    })
  })
}

canvas.save = filePath => {
  return new Promise((resolve, reject) => {
    uni.saveImageToPhotosAlbum({
      filePath,
      success: result => {
        console.log('saveImageToPhotosAlbum success', result)
        resolve(result)
      },
      fail: error => {
        console.log('saveImageToPhotosAlbum error', error)
        reject(error)
      },
    })
  })
}

export default canvas
